'use client'

import { useState, useEffect, useRef } from 'react'
import axios from 'axios'
import { Search, Loader2, ArrowRight, ChevronDown, Check, Plus, TrendingUp, BarChart3, AppWindow } from 'lucide-react'
import { motion, AnimatePresence } from 'framer-motion'
import Link from 'next/link'

const countries = [
    { code: 'us', name: 'United States', flag: 'ðŸ‡ºðŸ‡¸' },
    { code: 'in', name: 'India', flag: 'ðŸ‡®ðŸ‡³' },
    { code: 'gb', name: 'United Kingdom', flag: 'ðŸ‡¬ðŸ‡§' },
    { code: 'ca', name: 'Canada', flag: 'ðŸ‡¨ðŸ‡¦' },
    { code: 'au', name: 'Australia', flag: 'ðŸ‡¦ðŸ‡º' },
    { code: 'de', name: 'Germany', flag: 'ðŸ‡©ðŸ‡ª' },
    { code: 'fr', name: 'France', flag: 'ðŸ‡«ðŸ‡·' },
    { code: 'br', name: 'Brazil', flag: 'ðŸ‡§ðŸ‡·' },
]

export default function AppSelectionPage() {
    const [searchTerm, setSearchTerm] = useState('')
    const [searchResults, setSearchResults] = useState([])
    const [isSearching, setIsSearching] = useState(false)
    const [country, setCountry] = useState('us')
    const [trendingApps, setTrendingApps] = useState([])
    const [isLoadingTrending, setIsLoadingTrending] = useState(false)
    const [isDropdownOpen, setIsDropdownOpen] = useState(false)
    const dropdownRef = useRef(null)

    useEffect(() => {
        const detectLocation = async () => {
            try {
                // Try lightweight service first (api.country.is) as it's very reliable for just country code
                // Fallback to ipapi.co if needed
                let detectedCode = null

                try {
                    const { data } = await axios.get('https://api.country.is')
                    detectedCode = data.country
                } catch (e) {
                    const { data } = await axios.get('https://ipapi.co/json/')
                    detectedCode = data.country_code
                }

                if (detectedCode) {
                    const code = detectedCode.toLowerCase()
                    if (countries.some(c => c.code === code)) {
                        setCountry(code)
                    }
                }
            } catch (error) {
                console.error('Location detection failed', error)
            }
        }
        detectLocation()

        const handleClickOutside = (event) => {
            if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
                setIsDropdownOpen(false)
            }
        }
        document.addEventListener('mousedown', handleClickOutside)
        return () => document.removeEventListener('mousedown', handleClickOutside)
    }, [])

    useEffect(() => {
        fetchTrending()
    }, [country])

    const fetchTrending = async () => {
        setIsLoadingTrending(true)
        try {
            const { data } = await axios.get(`/api/apps/charts?country=${country}`)
            setTrendingApps(data)
        } catch (error) {
            console.error('Failed to fetch trending')
        } finally {
            setIsLoadingTrending(false)
        }
    }

    const handleSearch = async (e) => {
        const term = e.target.value
        setSearchTerm(term)
        if (term.length < 3) {
            setSearchResults([])
            return
        }

        setIsSearching(true)
        try {
            const { data } = await axios.get(`/api/apps/search?term=${term}&country=${country}`)
            setSearchResults(data)
        } catch (error) {
            console.error('Search failed')
        } finally {
            setIsSearching(false)
        }
    }

    const selectedCountry = countries.find(c => c.code === country)

    return (
        <div className="max-w-6xl mx-auto px-6 py-12 space-y-16">
            {/* Minimalist Hero Section */}
            <header className="space-y-4 max-w-2xl">
                <div className="inline-flex items-center gap-2 px-3 py-1 rounded-full bg-blue-500/10 border border-blue-500/20 mb-2">
                    <BarChart3 className="w-4 h-4 text-blue-500" />
                    <span className="text-[10px] font-black uppercase tracking-widest text-blue-500">Professional App Intelligence</span>
                </div>
                <h1 className="text-5xl font-extrabold tracking-tight text-white leading-tight">
                    Analyze. Track. <span className="text-blue-500 underline decoration-blue-500/30 underline-offset-8">Outperform.</span>
                </h1>
                <p className="text-lg text-slate-400 font-medium">
                    The efficient way to monitor keyword rankings and competitor visibility on the Play Store.
                </p>
            </header>

            {/* Search Bar Section - High Efficiency */}
            <div className="flex flex-col md:flex-row gap-4">
                <div className="relative flex-1 group">
                    <Search className="absolute left-5 top-1/2 -translate-y-1/2 text-slate-500 w-5 h-5 group-focus-within:text-blue-500 transition-colors" />
                    <input
                        type="text"
                        placeholder="Search for an app by name or ID..."
                        className="pro-input pl-14 text-lg"
                        value={searchTerm}
                        onChange={handleSearch}
                    />
                    {isSearching && (
                        <div className="absolute right-5 top-1/2 -translate-y-1/2">
                            <Loader2 className="w-5 h-5 animate-spin text-blue-500" />
                        </div>
                    )}
                </div>

                <div className="relative" ref={dropdownRef}>
                    <button
                        onClick={() => setIsDropdownOpen(!isDropdownOpen)}
                        className="flex items-center gap-3 pro-input text-base font-semibold min-w-[200px] justify-between"
                    >
                        <div className="flex items-center gap-2">
                            <span>{selectedCountry?.flag}</span>
                            <span>{selectedCountry?.name}</span>
                        </div>
                        <ChevronDown className={`w-4 h-4 text-slate-500 transition-transform ${isDropdownOpen ? 'rotate-180' : ''}`} />
                    </button>

                    <AnimatePresence>
                        {isDropdownOpen && (
                            <motion.div
                                initial={{ opacity: 0, y: 5, scale: 0.98 }}
                                animate={{ opacity: 1, y: 0, scale: 1 }}
                                exit={{ opacity: 0, y: 5, scale: 0.98 }}
                                className="absolute right-0 mt-2 w-full bg-slate-900/80 backdrop-blur-xl border border-white/10 rounded-xl shadow-2xl z-50 p-2 overflow-hidden"
                            >
                                <div className="max-h-[300px] overflow-y-auto no-scrollbar">
                                    {countries.map((c) => (
                                        <button
                                            key={c.code}
                                            onClick={() => {
                                                setCountry(c.code)
                                                setIsDropdownOpen(false)
                                            }}
                                            className={`flex items-center justify-between w-full px-4 py-2.5 rounded-lg text-sm font-medium transition-all ${country === c.code ? 'bg-blue-500/10 text-white' : 'hover:bg-white/5 text-slate-400'}`}
                                        >
                                            <div className="flex items-center gap-2">
                                                <span>{c.flag}</span>
                                                <span>{c.name}</span>
                                            </div>
                                            {country === c.code && <Check className="w-4 h-4 text-blue-500" />}
                                        </button>
                                    ))}
                                </div>
                            </motion.div>
                        )}
                    </AnimatePresence>
                </div>
            </div>

            {/* Results / Trending Display */}
            <main>
                <AnimatePresence mode="wait">
                    {searchResults.length > 0 ? (
                        <motion.div
                            key="results"
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4"
                        >
                            {searchResults.map((app) => (
                                <Link
                                    key={app.appId}
                                    href={`/tracker?appId=${app.appId}&title=${encodeURIComponent(app.title)}&icon=${encodeURIComponent(app.icon)}&developer=${encodeURIComponent(app.developer)}&country=${country}`}
                                >
                                    <div className="glass-card p-4 flex items-center gap-4 group cursor-pointer border-white/5 hover:border-blue-500/30">
                                        <img src={app.icon} className="w-12 h-12 rounded-xl shadow-lg" alt="" />
                                        <div className="flex-1 min-w-0">
                                            <h3 className="font-bold text-slate-100 truncate group-hover:text-blue-400 transition-colors uppercase text-xs tracking-wide">{app.title}</h3>
                                            <p className="text-[10px] text-slate-500 font-bold truncate mt-0.5">{app.developer}</p>
                                        </div>
                                        <div className="bg-white/5 p-2 rounded-lg opacity-0 group-hover:opacity-100 transition-opacity">
                                            <ArrowRight className="w-4 h-4 text-blue-500" />
                                        </div>
                                    </div>
                                </Link>
                            ))}
                        </motion.div>
                    ) : (
                        searchTerm.length < 3 && (
                            <motion.div key="trending" initial={{ opacity: 0 }} animate={{ opacity: 1 }} className="space-y-8">
                                <div className="flex items-center gap-3 border-b border-white/5 pb-4">
                                    <TrendingUp className="w-5 h-5 text-orange-500" />
                                    <h2 className="text-sm font-black text-white uppercase tracking-[0.2em]">Market Pulse: {selectedCountry?.name}</h2>
                                    {isLoadingTrending && <Loader2 className="w-4 h-4 animate-spin text-slate-600" />}
                                </div>

                                <div className="grid grid-cols-1 md:grid-cols-2 gap-x-12 gap-y-2">
                                    {trendingApps.map((app, index) => (
                                        <Link
                                            key={app.appId}
                                            href={`/tracker?appId=${app.appId}&title=${encodeURIComponent(app.title)}&icon=${encodeURIComponent(app.icon)}&developer=${encodeURIComponent(app.developer)}&country=${country}`}
                                        >
                                            <div className="flex items-center gap-4 py-3 group cursor-pointer border-b border-white/[0.03]">
                                                <span className="text-xl font-black text-slate-800 group-hover:text-blue-500 transition-colors w-6">
                                                    {(index + 1).toString().padStart(2, '0')}
                                                </span>
                                                <img src={app.icon} className="w-10 h-10 rounded-lg grayscale group-hover:grayscale-0 transition-all shadow-md" alt="" />
                                                <div className="flex-1 min-w-0">
                                                    <h3 className="font-bold text-slate-300 truncate group-hover:text-white transition-colors text-xs">{app.title}</h3>
                                                    <p className="text-[10px] text-slate-500 font-bold truncate">{app.developer}</p>
                                                </div>
                                                <div className="text-[10px] font-black text-slate-700 uppercase p-1">View Details</div>
                                            </div>
                                        </Link>
                                    ))}
                                </div>
                            </motion.div>
                        )
                    )}
                </AnimatePresence>
            </main>

            {/* Empty State Refinement */}
            {searchResults.length === 0 && !isSearching && searchTerm.length >= 3 && (
                <div className="flex flex-col items-center justify-center py-24 text-center">
                    <div className="bg-slate-900/50 p-6 rounded-3xl mb-4 border border-white/5">
                        <AppWindow className="w-10 h-10 text-slate-700" />
                    </div>
                    <h3 className="text-xl font-bold text-white uppercase italic">No Matches Found</h3>
                    <p className="text-slate-500 text-sm max-w-xs mt-1">We couldn't find an app matching your query. Please check the spelling or try the full package name.</p>
                </div>
            )}
        </div>
    )
}
